.body {
  background-color: #ffffff;
  padding: 100px 0 100px;
}

.form-control {
  width: 50%;
  border: 1px solid #ccc;
  padding: 10px;
}

.control-error {
  border: 1px solid red;
}

.form {
  margin: 75px auto 0;
  max-width: 800px;
}

.form h1{
  color: #282c34;
  align-self: flex-start;
}

.form a{
  color: #282c34;
}

.row {
  display: flex;
}

.first {
  width: 300px;
  margin-right: 50px;
  margin-top: 5px;
}

.second {
  width: 300px;
  margin-top: 5px;
}

.contactFooter{
  position: absolute;
  width: 80%;
  height: 100px;
  left:50%;
  transform: translate(-50%,75%);
  color: white;
  text-align: center;
}

.contactFooter span{
  color: white;
  padding: 10px;
}