h1, h2, p, ul, li {
  font-family: sans-serif;
}
ul.mainheader li {
  display: inline;
  list-style-type: circle;
  margin: 0;
}
ul.mainheader {
  background-color: #000000;
  position: absolute;
  z-index:10;
  left: 0;
  top:0px;
  width: 100%;
  height: 75px;
}

div.image {
  height: 0px;
  width: 0px;
}

ul.mainheader li a {
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 30px 5px;
  display: inline-block;
  float: right;
  margin-right: 10%;

}

ul.mainheader li a:hover {
  color: red;
}

ul.mainheader img {
  float: left;
  height: 50px;
  max-width:100%;
  margin-left: -10px;
  margin-top: 10px;
}

.mainContent {
  background-color: #000000;
  /*padding-top: -20px;*/
}
.mainContent h2 {
  padding: 0;
  margin: 0;
}
.mainContent li {
  margin-bottom: 10px;
}

.box {
  /*left: -20px;*/
  min-height:100%;
  position:relative;
}

.homeCarousel{
  /*position: absolute;*/
  /*display: block;*/
  /*left: 0;*/
  /*bottom: 200px;*/
  pointer-events: auto;
  margin: 0;
  height: 100%;
  padding: 75px 0 275px;

}

.homeCarousel img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.lineSpan{
  color: white;
  width: 100%;
  font-size: 24px;
  font-family: "Arial Black";
  text-align: center;
  position: absolute;
  bottom: 225px;
}

.appFooter{
  width: 100%;
  /*background: rgba(0, 0, 0, 0.5);*/
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  height: 150px;
  bottom: 100px;

}

.appFooter img{
  margin: 10px;
}

.mainFooter{
  position: absolute;
  width: 100%;
  height: 100px;
  padding: 25px;
  bottom: 25px;
  color: white;
  text-align: center;

}

.mainFooter span{
  color: white;
  padding: 10px;
}

