@media only screen and (max-width: 550px) {
  .mainheader img {
    position: absolute;
    left: 15px;
    top: 5px;
  }
}

@media only screen and (max-width: 450px) {
  .appFooter {
    height: 200px;
    flex-direction: column;
    bottom: 0px;
  }
  .appFooter img {
    width: 250px;
  }
  .mainFooter{
    bottom: -100px;
  }
}


@media only screen and (max-height: 380px) {
  .homeCarousel{
    bottom: 100px;
  }
  .mainFooter {
    bottom: 25px;
  }
  .appFooter{
    height: 150px;
    bottom: 100px;
  }
}

@media only screen and (max-width: 350px) {
  .mainheader img{
    position: absolute;
    width: 130px;
    left: 15px;
    top: 5px;
  }
}